import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FilterOptionType, FilterReferenceItem } from '@schemas/modules/core/filters/service-filter';
import { FilterService } from '@services/modules/inactivity/filter.service';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-results-filters',
    templateUrl: './filters.component.html',
    styleUrls: ['./filters.component.scss'],
})
export class FiltersComponent implements OnInit {
    @Input() filter: FilterReferenceItem = [];
    @Input() loading = false;
    @Input() disabled = false;
    @Output() filterChanged: EventEmitter<FilterReferenceItem> = new EventEmitter<FilterReferenceItem>();
    public filterOptionType = FilterOptionType;

    constructor(public filterService: FilterService, public platformService: PlatformService) {}

    ngOnInit(): void {}

    public onFilterChanged(): void {
        this.filterChanged.emit(this.filter);
    }

    public enableClearAll(): boolean {
        return this.filter.some((filterItem) => !filterItem.hide && FilterService.checkEnableClear(filterItem));
    }

    public onClearAll(): void {
        for (const filterItem of this.filter) {
            FilterService.clearAll(filterItem);
        }
        this.onFilterChanged();
    }
}
