import { Component, OnInit } from '@angular/core';
import { ModalService } from '@services/modal.service';
import { SortByValueOption } from '@schemas/modules/core/sort-by-value.option';

@Component({
    selector: 'tz-mob-sort-results-modal',
    templateUrl: './mob-sort-results-modal.component.html',
    styleUrls: ['./mob-sort-results-modal.component.scss'],
})
export class MobSortResultsModalComponent implements OnInit {
    public data!: { sortByOptions: SortByValueOption[]; sortKey: string };
    public sort!: string;

    constructor(private modalService: ModalService) {}

    ngOnInit() {
        this.sort = this.data.sortKey;
    }

    public onSort(): void {
        this.modalService.dismiss(this.sort);
    }

    public onSortChange(event: Event): void {
        this.sort = (event as CustomEvent).detail.value;
    }
}
