import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'tz-filter-skeleton-loader',
    templateUrl: './filter-skeleton-loader.component.html',
    styleUrls: ['./filter-skeleton-loader.component.scss'],
})
export class FilterSkeletonLoaderComponent implements OnInit {
    public filters!: number[];
    public filterItems!: number[];
    private filtersCount = 12;
    private filterItemsCount = 4;

    constructor() {}

    ngOnInit() {
        this.setFilters();
    }

    private setFilters(): void {
        this.filters = new Array(this.filtersCount);
        this.filterItems = new Array(this.filterItemsCount);
    }
}
