import { Component, HostListener, Input, OnInit } from '@angular/core';
import { ModalService } from '@services/modal.service';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { ServiceSummerColorfulIcons, ServiceSummeryWhiteIcons } from '@core/constants/service-icons.const';
import { ServiceTab } from '@schemas/modules/service-tabs/service-tab';
import { ServiceId } from '@core/constants/service-id.const';
import { SelectedLuggageAmount } from '@schemas/modules/checkout/transport-luggage';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ICartItem } from '@schemas/modules/cart/cart-item';

@Component({
    selector: 'tz-mob-summery-modal',
    templateUrl: './mob-summery-modal.component.html',
    styleUrls: ['./mob-summery-modal.component.scss'],
})
export class MobSummeryModalComponent implements OnInit {
    @Input() public luggageAmount!: SelectedLuggageAmount;
    @Input() public additionalLuggages: { type: string; luggageText: string }[] = [];
    public tabsServiceIds: Set<number> = new Set<number>();
    public baskedTab: ServiceTab | undefined;
    public tabs: ServiceTab[] = [];
    public servicesIcons: { [key: number]: string } = ServiceSummeryWhiteIcons;
    public servicesColorfulIcons: { [key: number]: string } = ServiceSummerColorfulIcons;
    public currentService: ServiceId = ServiceId.INVALID;
    public cartSummary: CheckoutCartServiceSummary[] = [];
    public serviceIDs = ServiceId;
    public cartData!: ICartItem[];
    public totalPersonsStr = '';
    private data!: { cartSummary: CheckoutCartServiceSummary[]; tabs: ServiceTab[]; totalPersonsStr: string };

    constructor(private modalService: ModalService, private cartService: CartService) {}

    @HostListener('window:popstate', ['$event'])
    dismissModal(): void {
        this.onCloseModal();
    }

    public ngOnInit(): void {
        this.initData();
    }

    public onCloseModal(): void {
        this.modalService.dismiss();
    }

    private initData(): void {
        this.modalService.historyPushModal();

        this.tabs = this.data.tabs;
        this.cartSummary = this.data.cartSummary;
        this.totalPersonsStr = this.data.totalPersonsStr;
        this.cartData = this.cartService.getCartData().items;

        this.baskedTab = this.tabs.find((serviceTab: ServiceTab) => serviceTab.serviceId === 8);

        this.cartSummary.forEach((dataCart: CheckoutCartServiceSummary) => {
            this.tabsServiceIds.add(dataCart.serviceId);
            dataCart.iconType = this.servicesColorfulIcons[dataCart.serviceId];
            dataCart.isVirtual = this.cartData.find(
                (serviceTab: ICartItem) => serviceTab.serviceId === dataCart.serviceId
            )?.isVirtual;
        });
    }
}
