import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
    selector: 'tz-filter-header-component',
    templateUrl: './filter-header-component.component.html',
    styleUrls: ['./filter-header-component.component.scss'],
})
export class FilterHeaderComponent implements OnInit {
    @Input()
    existingFilter = false;
    @Output()
    clearAll: EventEmitter<void> = new EventEmitter<void>();

    constructor() {}

    ngOnInit(): void {}

    public onClearAll(): void {
        this.clearAll.emit();
    }
}
