import { Component, OnDestroy, OnInit } from '@angular/core';
import { FilterReferenceItem } from '@schemas/modules/core/filters/service-filter';
import { BehaviorSubject, Subject } from 'rxjs';
import { ModalService } from '@services/modal.service';
import { FilterService } from '@services/modules/inactivity/filter.service';
import { takeUntil } from 'rxjs/operators';

@Component({
    selector: 'tz-mob-filter-modal',
    templateUrl: './mob-filter-modal.component.html',
    styleUrls: ['./mob-filter-modal.component.scss'],
})
export class MobFilterModalComponent implements OnInit, OnDestroy {
    public filter!: FilterReferenceItem;
    public filter$!: BehaviorSubject<FilterReferenceItem>;
    public resultsCount$!: Subject<number>;
    public data: any;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(private modalService: ModalService, public filterService: FilterService) {}

    get isEnableClearFilter(): boolean {
        return FilterService.enableClearAll(this.filter);
    }

    ngOnInit() {
        this.filter$ = this.data.filter;
        this.filter = this.filter$.getValue();
        this.resultsCount$ = this.data.resultsCount$;

        this.filterService.filterChange$.pipe(takeUntil(this.destroy$)).subscribe((filter) => {
            this.filter = filter;
        });
    }

    public onFilterChanged(filter: FilterReferenceItem): void {
        this.filter$.next(filter);
        this.filter = filter;
    }

    public onShowResults(): void {
        this.modalService.dismiss();
    }

    public onResetFilter(): void {
        for (const filterItem of this.filter) {
            FilterService.clearAll(filterItem);
        }
        this.filter$.next(this.filter);
    }

    public ngOnDestroy() {
        this.destroy$.next(true);
        this.destroy$.complete();
    }
}
