import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'tz-filter-footer',
    templateUrl: './filter-footer.component.html',
    styleUrls: ['./filter-footer.component.scss'],
})
export class FilterFooterComponent {
    @Input() resultsCount!: number;
    @Output() showResults: EventEmitter<void> = new EventEmitter<void>();

    onShowResults(): void {
        this.showResults.emit();
    }
}
