import { Component, EventEmitter, OnInit, Input, Output } from '@angular/core';

import { FilterItem, FilterOptionType } from '@schemas/modules/core/filters/service-filter';
import { FilterService } from '@services/modules/inactivity/filter.service';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-filter-options',
    templateUrl: './filter-options.component.html',
    styleUrls: ['./filter-options.component.scss'],
})
export class FilterOptionsComponent implements OnInit {
    @Input() filterItem!: FilterItem;
    @Input() optionType!: FilterOptionType;
    @Input() isFirst = false;
    @Output() filterItemChange: EventEmitter<FilterItem> = new EventEmitter<FilterItem>();
    public filterOptionType = FilterOptionType;
    public isMobileStops = false;
    public titleText = '';
    public displayRangeCount = false;

    constructor(public filterService: FilterService, public platformService: PlatformService) {}

    public get staticFilter() {
        return FilterService;
    }

    ngOnInit(): void {
        // console.log('this.filterItem', this.filterItem);
        this.setFilterTitle();
        this.setMobStarsOrder();
        this.setMobDisplayRangeCount();
        this.isMobileStops =
            this.platformService.isMobileDevice &&
            (this.filterItem.ref_id === 'TRAIN_STOPS' || this.filterItem.ref_id === 'FLIGHT_STOPS');
    }

    public onFilterItemChanged(): void {
        this.filterItemChange.emit(this.filterItem);
    }

    public clearAll(): void {
        FilterService.clearAll(this.filterItem);
        this.onFilterItemChanged();
    }

    private setFilterTitle(): void {
        this.titleText = this.filterItem.ref_id;
    }

    private setMobStarsOrder(): void {
        if (this.platformService.isMobileDevice && this.filterItem.ref_id === 'ACC_CATEGORY') {
            this.filterItem.checkboxes = this.filterItem.checkboxes
                ?.filter((checkbox) => checkbox.ref_id !== '0_STAR')
                .reverse();
        }
    }

    private setMobDisplayRangeCount(): void {
        if (this.platformService.isMobileDevice) {
            this.displayRangeCount =
                this.filterItem.ref_id === 'BUDGET' ||
                this.filterItem.ref_id === 'ACC_CUSTOMER_RATING' ||
                this.filterItem.ref_id === 'ACC_CITY_CENTER';
        }
    }
}
