export type FilterReferenceItem = Array<FilterItem>;

export interface FilterItem {
    checkboxes?: FilterItemCheckbox[];
    default_selection: boolean;
    hide: boolean;
    hide_on_zero: boolean;
    idle: boolean;
    limit_to: number;
    name: string;
    option_type: FilterOptionType;
    ordinal: number;
    ref_id: string;
    results_count: number;
    selectAll: boolean;
    show_clear_option: boolean;
    show_more_count: number;
    skipEmpty: boolean;
    sort_by: number;
    slider?: FilterSlider;
    isStar: boolean;
    sections?: FilterSection[];
}

export interface FilterItemCheckbox {
    count: number;
    disabled: boolean;
    enable_price: boolean;
    hide: boolean;
    min_price: string;
    min_price_cur: string;
    name: string;
    ordinal: number;
    ref_id: string;
    selected: boolean;
    value: string;
}

export interface FilterSlider {
    conversion_unit: string;
    max: number;
    min: number;
    money: {
        min: number;
        max: number;
        symbol: string;
    };
    options: FilterSliderOptions;
    selected: boolean;
    selected_max: number;
    selected_min: number;
    slider_type: FilterSliderOptionType;
    unit: string;
    unit_type: FilterSliderUnitType;
}

export interface FilterSliderOptions {
    floor: number;
    ceil: number;
    minLimit: number;
    maxLimit: number;
    step: number;
    precision: number;
    disabled: boolean;
}

export interface FiltersShowMoreOptions {
    [key: string]: {
        showAll?: boolean;
        showMore?: boolean;
        showLess?: boolean;
        limit: number;
    };
}

export interface FilterShowMoreOptions {
    id: string;
    limitTo: number;
    itemsCount: number;
}

export interface FilterSection {
    active: boolean;
    disabled: boolean;
    name: string;
    ogs: FilterItem[];
}

export enum FilterOptionType {
    CHECKBOX = 1,
    SLIDER,
    SECTION,
    RADIO,
}

export enum FilterSliderOptionType {
    SINGLE = 1,
    DOUBLE,
}

export enum FilterSliderUnitType {
    UNKNOWN = 0,
    CURRENCY,
    DISTANCE,
    DATE,
    DURATION,
}
