import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { BehaviorSubject, Subject } from 'rxjs';
import { FilterReferenceItem } from '@schemas/modules/core/filters/service-filter';
import { IHotelNames } from '@schemas/modules/accommodations/accommodations-response';

@Component({
    selector: 'tz-filter-dialog',
    templateUrl: './filter-dialog.component.html',
    styleUrls: ['./filter-dialog.component.scss'],
})
export class FilterDialogComponent implements OnInit {
    public filter: FilterReferenceItem;
    public filter$: BehaviorSubject<FilterReferenceItem>;
    public resultsCount$: Subject<number>;
    public searchHotel?: {
        hotelNames: IHotelNames[];
        searchHotel$: Subject<IHotelNames>;
    };

    constructor(@Inject(MAT_DIALOG_DATA) public data: any, private mdDialogRef: MatDialogRef<FilterDialogComponent>) {
        this.filter$ = this.data.filter;
        this.filter = this.filter$.getValue();
        this.resultsCount$ = data.resultsCount$;

        if (data.searchHotel) {
            this.searchHotel = data.searchHotel;
        }
    }

    ngOnInit(): void {}

    public onFilterChanged($event: FilterReferenceItem): void {
        this.filter$.next($event);
    }

    public searchHotelName(searchedHotel: IHotelNames): void {
        this.searchHotel?.searchHotel$.next(searchedHotel);
        this.showResults();
    }

    public showResults(): void {
        this.mdDialogRef.close();
    }
}
